const sweatNaFiles = [
  {
    id: 1,
    name: "_20241201-20251028012345.csv",
    status: "",
    updateTime: "2024-12-01T10:30:00Z"
  },
  {
    id: 2,
    name: "_20241202-20251028012345.csv",
    status: "",
    updateTime: "2024-12-02T14:15:00Z"
  },
  {
    id: 3,
    name: "_20241203-20251028012345.csv",
    status: "",
    updateTime: "2024-12-03T09:45:00Z"
  },
  {
    id: 4,
    name: "_20241204-20251028012345.csv",
    status: "",
    updateTime: "2024-12-04T16:20:00Z"
  }
];

export default function (mock) {
  // Get files list
  mock.onGet("/api/Files/SweatNa").reply(200, sweatNaFiles);

  // Upload files
  mock.onPost("/api/Files/SweatNa").reply((config) => {
    const newFile = {
      id: sweatNaFiles.length + 1,
      name: `SweatNa_${new Date().toISOString().split('T')[0]}-20251028012345.csv`,
      updateTime: new Date().toISOString()
    };
    
    sweatNaFiles.push(newFile);
    
    return [200, {
      success: true,
      data: newFile,
      message: ""
    }];
  });

  // Preview file content
  mock.onGet(/\/api\/Files\/SweatNa\/.+/).reply(config => {
    const name = decodeURIComponent(config.url.split('/').pop());
    // Simple mock content, you can customize per file name if needed
    const content = `id,name,value
1,Alice,123
2,Bob,456
3,Carol,789`;
    return [200, { content }];
  });

}
