import axios from "@/axios";
import AxiosMockAdapter from "axios-mock-adapter";
import system from "./system";
import users from "./users";
import authentication from "./authentication";
import dashboardPsychology from "./dashboard/psychology";
import dashboardPhysiology from "./dashboard/physiology";
import dashboardNutrition from "./dashboard/nutrition";
import unit from "./unit";
import file from "./file";
import useruser from "./useruser";

const mock = new AxiosMockAdapter(axios, { delayResponse: 100 });

users(mock);
system(mock);
authentication(mock);
unit(mock);
file(mock);
useruser(mock);
dashboardPsychology(mock);
dashboardPhysiology(mock);
dashboardNutrition(mock);

mock.onAny().passThrough();

export default axios;
